import * as path from 'node:path';
import { fileURLToPath } from 'node:url';
import { deepmerge } from 'deepmerge-ts';
/**
 * Modules
 */
export const filename = (url) => fileURLToPath(url);
export const dirname = (url) => path.dirname(filename(url));
/**
 * Objects
 */
export const merge = (x, y) => {
    return deepmerge(x, y);
};
/**
 * Git
 */
export const convertValidBranchName = (input) => {
    let b = input.trim();
    b = b.replace(/^[./]+/, ''); // remove prefix '.' or '/'
    b = b.replace(/[/]+/g, '-'); // convert '/' to '-'
    b = b.replace(/[@{\\~^:?*[\]]+/g, ''); // remove invalid character
    b = b.replace(/[.]+$/, ''); // remove "." at the end of string
    return b;
};
